/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Link } from "react-router-dom";


const Header = () => {
    const [isActive, setisActive] = React.useState(false);  
    return(
        <header className="container is-max-widescreen">
                <nav className="navbar nav-head" role="navigation" aria-label="main navigation">
                    <div className="navbar-brand">
                        <Link to="/home">
                            <img className="img-center" src={require("../image/header/logo.jpg").default} alt="logo" />
                        </Link>
                        <a  onClick={() => {setisActive(!isActive);}} role="button" className={`navbar-burger burger ${isActive ? "is-active" : ""}`} aria-label="menu" aria-expanded="false" data-target="navbarBasicExample">
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                            <span aria-hidden="true"></span>
                        </a>
                    </div>
                    <div className={`navbar-menu ${isActive ? "is-active" : ""}`} id="navbarBasicExample">
                        <div className="navbar-end ">
                            <div className="navbar-item has-dropdown is-hoverable"  >
                                <a className="navbar-link"></a>
                                <div class="navbar-dropdown">
                                    <a className="navbar-item">
                                        <Link to="/atayal"></Link> 
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/saisiyat"></Link>
                                    </a>   
                                </div>
                            </div>
                            <div className="navbar-item has-dropdown is-hoverable" >
                                <a className="navbar-link"></a>
                                <div class="navbar-dropdown">
                                    <a className="navbar-item" > 
                                        <Link to="/bunun"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/thao"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/tsou"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/seediq"></Link>
                                    </a>
                                </div>
                            </div>
                            <div className="navbar-item has-dropdown is-hoverable" >
                                <a className="navbar-link"></a>
                                <div class="navbar-dropdown">
                                    <a className="navbar-item" >
                                        <Link to="/paiwan"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/rukai"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/kanakanavu"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/hlaalua"></Link>
                                    </a>
                                </div>
                            </div>
                            <div className="navbar-item has-dropdown is-hoverable" > 
                                <a className="navbar-link"></a>
                                <div class="navbar-dropdown">
                                    <a className="navbar-item" >
                                        <Link to="/pangcah"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/puyuma"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/kavalan"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/truku"></Link>
                                    </a>
                                    <a className="navbar-item" >
                                        <Link to="/sakizaya"></Link>
                                    </a>                                
                                </div>
                            </div>
                            <div className="navbar-item has-dropdown is-hoverable" > 
                                <a className="navbar-link"></a>
                                <div class="navbar-dropdown">
                                    <a className="navbar-item" >
                                        <Link to="/yami"></Link>
                                    </a>                                
                                </div>
                            </div>  
                        </div>
                    </div>
                </nav>
        </header>
    );
}
export default Header;